<template>
  <div>
    <v-dialog max-width="32rem" v-model="dialog">
      <template v-slot:activator="{ on }">
        <v-btn text v-on="on" class="purple white--text ma-2 ml-4"
          > </v-btn
        >
      </template>
      <v-card>
        <v-list-item two-line>
          <v-list-item-content>
            <v-list-item-title class="ma-2">
              <p class="text-h5 text-center"> </p>
            </v-list-item-title>
            <v-list-item-subtitle>
              <p class="subheading text-center">
                Lee   . <br />
                    .
              </p>
            </v-list-item-subtitle>
          </v-list-item-content>
        </v-list-item>
        <v-card-text>
          <v-form class="pa-1" ref="form">
            <v-text-field
              label=""
              v-model.lazy="message.name"
              placeholder="  "
              prepend-icon="mdi-id-card"
              required
            ></v-text-field>
            <v-text-field
              label=""
              v-model.lazy="message.title"
              placeholder=""
              prepend-icon="mdi-format-title"
              required
            ></v-text-field>
            <v-textarea
              label=""
              v-model.lazy="message.content"
              placeholder=""
              prepend-icon="mdi-text-box-outline"
              required
            >
            </v-textarea>
            <v-flex xs12 sm6 offset-sm1>
              <v-btn raised class="primary" @click="onPickFile"
                > </v-btn
              >
              <input
                type="file"
                style="display: none"
                ref="fileInput"
                accept="image/jpg"
                @change="onProfilePictureChange"
              />
              <p id="image_title" class="mt-2"></p>
            </v-flex>
            <v-text-field
              label="   "
              v-model.lazy="message.contact"
              prepend-icon="mdi-email"
              required
            ></v-text-field>
            <v-btn text class="error ma-2" @click="reset"></v-btn>
            <v-btn text class="success ma-2" @click="submit" :loading="loading"
              ></v-btn
            >
          </v-form>
        </v-card-text>
      </v-card>
    </v-dialog>
  </div>
</template>
<script>
import { db, storage } from "../../firebase.js";
//  will be auto-generated in logic part
export default {
  /* eslint-disable */
  data() {
    return {
      message: {
        contact: '',
        name: '',
        title: '',
        content: '',
        photo: '',
        photo_name: '',
      },
        loading: false,
        dialog: false,
    };
  },
  methods: {
    reset: function () {
      this.message.contact = '';
      this.message.name = '';
      this.message.title = '';
      this.message.content = '';
      this.message.photo = undefined;
    },
    onPickFile: function () {
      this.$refs.fileInput.click();
    },
    onProfilePictureChange: function (e) {
      const {target: {files}} = e;
      if(files[1]) {
        alert(".   .");
        return;
      }
      const filename = files[0].name;
      if (filename.lastIndexOf('.') <= 0) {
        alert("  .");
        return;
      }
      this.photo_name = filename;
      const fileReader = new FileReader();
      fileReader.addEventListener('load', () => {
        this.message.photo = fileReader.result;
      })
      fileReader.readAsDataURL(files[0]);
      this.message.photo = files[0];
      
      const image_title = document.querySelector("#image_title");
      image_title.innerText = `: ${this.message.photo.name}`
    },
    submit: function () {
      const stuff = {
        contact: this.message.contact,
        name: this.message.name,
        title: this.message.title,
        content: this.message.content,
        photo: this.message.photo,
        date: new Date(),
      };
      if (stuff?.contact === ''
            || stuff.name === ''
            || stuff.title === ''
            || stuff.content === '') {
                alert("   .\n" +
                        "   .");
                return;
            }
      if (this.$refs.form.validate()) {
        this.loading = true;
        db.collection("Personal Message")
          .add(stuff)
          .then(() => {
            alert("  !");
            this.loading = false;
            this.dialog = false;
            if(stuff.photo) {
                const ref = storage.ref();
                const go = ref.child("Personal Message")
                                .child(`${stuff.name}/${stuff.date}/${this.photo_name}`)
                                .putString(stuff.photo, "data_url");
                go.on('state_changed', snapshot => {
                    console.log(snapshot);
                })                
            }
          });
      } else {
        alert("    .");
      }
    },
  },
};
</script>
<style></style>
